import React, { Component } from "react"
import { fabric } from "fabric"
import { ButtonToolbar, Button } from "react-bootstrap"

class FabricCanvas extends Component {
  constructor(props) {
    super(props)

    this.state = {
      add: true,
      lastCacheKey: "",
      isDisabled: false
    }
  }

  componentDidMount() {
    // Make a New Canvas
    this.the_canvas = new fabric.Canvas("main-canvas", {
      selection: true,
      preserveObjectStacking: false,
      height: 400,
      width: 400,
    })

    // Canvas Event Handler
    this.the_canvas.on({
      "object:selected": this.onSelected,
      "mouse:up": this.onMouseUp
    })
  }

  componentWillReceiveProps = newprops => {
    console.log(this.the_canvas.getObjects().length)
    this.isDisabled = this.the_canvas.getObjects().length <= 0
    // If Updated Item is not the same as the old one
    // 		=> Update the canvas with newer item
    if (newprops.activeProperty !== this.props.activeProperty) {
      this.updateCanvasforImage(
        this.props.activeProperty,
        newprops.activeProperty
      )
    }
  }

  updateCanvasforImage = (prev, next) => {
    if (next) {

      if (next.the_type === "items") {
        this.the_canvas.setBackgroundImage(next)
        this.the_canvas.renderAll()
        return
      }

      // Add image flg changed
      this.add = true
      // Last image saved
      this.lastCacheKey = next.cacheKey
      // this.the_canvas.add(next)
      // this.the_canvas.moveTo(next, next.zIndex)
    }
  }

  saveToCanvas = () => {
    let link = document.createElement("a")
    link.href = this.the_canvas.toDataURL({ format: "jpg" })
    link.download = "new_create_image.jpg"
    link.click()
  }

  previousCanvas = () => {
    if(this.the_canvas.getObjects().length) return
    let to_remove
    // Find the previous of element
    this.the_canvas.forEachObject((object, key) => {
      if (Object.is(this.the_canvas.getObjects().length - 1, key)) {
        to_remove = object
      }
      if (this.last === object.cacheKey) {
        to_remove = object
      }
    })

    this.the_canvas.remove(to_remove)
    // Add image flg changed
    this.add = true
    // Last image saved
    const index = Object.is(this.the_canvas.getObjects().length)
    this.lastCacheKey = this.the_canvas.getObjects(index).cacheKey
  }

  resetCanvas = () => {
    this.the_canvas.clear()
  }

  onMouseUp = options => {
    if((this.props.activeProperty && this.props.activeProperty.the_type === "items") || options.target !== null || !this.add) return
    const pointer = this.the_canvas.getPointer();
    const left = pointer.x - (this.props.activeProperty.width / 2)
    const top = pointer.y - (this.props.activeProperty.height / 2)
    this.props.activeProperty.set({left: left, top: top})
    this.the_canvas.add(this.props.activeProperty);
    // Lock rotation and scaling
    const length = this.the_canvas.getObjects().length - 1
    this.the_canvas.item(length).hasControls = this.the_canvas.item(length).hasBorders = false;
    this.the_canvas.item(length).lockRotation = true
    this.the_canvas.item(length).lockScalingX = this.the_canvas.item(length).lockScalingY = true
    // Add image flg changed
    this.add = false
    // Last image saved
    this.lastCacheKey = this.props.activeProperty.cacheKey
    // Back button status changed
    this.isDisabled = this.the_canvas.getObjects().length <= 0
    console.log(this.the_canvas.getObjects().length)
  }

  onSelected = options => {
    options.target.bringToFront()
  }

  render() {
    return (
      <div className="main-canvas-container">
        <canvas id="main-canvas" />

        <ButtonToolbar
          style={{
            marginTop: `15px`,
          }}
        >
          <Button variant="info" onClick={this.saveToCanvas} size="lg">
            ダウンロード
          </Button>
          <Button
            variant="light"
            onClick={this.resetCanvas}
            size="lg"
            style={{
              marginLeft: `15px`,
            }}
          >
            クリア
          </Button>
          <Button
            variant="light"
            onClick={this.previousCanvas}
            size="lg"
            style={{
              marginLeft: `15px`,
            }}
            disabled={this.isDisabled}
          >
            戻る
          </Button>
        </ButtonToolbar>
      </div>
    )
  }
}

export default FabricCanvas
